
ul.list-inline li[data-emoji] {
  padding: 0rem 0rem;
}
ul.list-inline li[data-emoji]:hover {
  cursor: pointer;
}
ul.list-inline li[data-emoji] a {
  vertical-align: middle;
}
[data-emoji] {
  font-size: 1.2rem;
  margin: 0 0.2rem;
}
.editArea [data-emoji]:hover {
  cursor: default;
}
img[data-emoji] {
  width: 1.6rem;
  height: 1.6rem;
}


