
.post-enter-active,
.post-leave-active {
  transition: opacity 1s;
}
.post-enter,
.post-leave-active {
  opacity: 0;
}
.post-move {
  transition: -webkit-transform 1s;
  transition: transform 1s;
  transition: transform 1s, -webkit-transform 1s;
}


